import * as React from "react";
import { useState } from "react";
import Button from "react-bootstrap/esm/Button";
import ReactJson from "react-json-view";
import { log4TSProvider } from "../config/ConfigLog4j";
const log = log4TSProvider.getLogger("settings-view.tsx");
const login = (event) => {
    event.preventDefault();
    const firstField = event.currentTarget[0];
    log.debug("Just about to store login details:" + firstField);
};
const handleSaveClick = (event) => {
    chrome.storage.local.get(null, function (items) {
        // Convert object to a string.
        var result = JSON.stringify(items);
        // Save as file
        var url = 'data:application/json;base64,' + btoa(result);
        chrome.downloads.download({
            url: url,
            filename: 'filename_of_exported_file.json'
        });
    });
};
const SettingsViewContent = () => {
    const [localData, setLocalData] = useState({});
    const [settings, setSettings] = useState({});
    const handleResetClick = (event) => {
        log.debug("reset...");
        chrome.storage.local.remove(['aggregates', 'chatEvents'], () => { });
        setSettings({});
        setLocalData({});
    };
    React.useEffect(() => {
        // first load, at startup
        chrome.storage.local.get({ aggregates: {}, chatEvents: [], ecologitsData: [], settings: settings }, (result) => {
            setLocalData(result);
        });
    }, []);
    React.useEffect(() => {
        // first load, at startup
        chrome.storage.local.get({ settings: settings }, (result) => {
            setSettings(result.settings);
        });
    }, []);
    chrome.storage.onChanged.addListener((changes, namespace) => {
        if (chrome.runtime.lastError) {
            log.error('Error retrieving metrics:', chrome.runtime.lastError);
            return;
        }
        for (let [key, { oldValue, newValue }] of Object.entries(changes)) {
            if (key === "settings") {
                if (newValue) {
                    setSettings(newValue);
                }
            }
        }
    });
    return React.createElement("div", null,
        React.createElement("h3", null, "Les param\u00E8tres de votre extension"),
        React.createElement("h4", null, "Parametres locaux"),
        React.createElement("div", { className: "settings" },
            React.createElement("div", { className: 'rounded-tile' },
                "Data depuis: ",
                React.createElement("br", null),
                " ",
                new Date(settings.installationTime).getDate() + "/" + new Date(settings.installationTime).getMonth() + 1 + "/" + new Date(settings.installationTime).getFullYear() + " " + new Date(settings.installationTime).getHours() + ":" + new Date(settings.installationTime).getMinutes()),
            React.createElement("div", null,
                React.createElement(Button, { onClick: handleResetClick }, "Reset")),
            React.createElement("div", null,
                React.createElement(Button, { onClick: handleSaveClick }, "Save"))),
        React.createElement("div", null,
            React.createElement(ReactJson, { src: localData })));
};
export default SettingsViewContent;
