import { log4TSProvider } from "./ConfigLog4j";
const log = log4TSProvider.getLogger("Analytics.ts");
const api_secret = "RPbJ2hFETaaMMMqnpW5qUA";
const measurement_id = "G-NS4HXSQ2KM";
let clientId;
let versionId;
async function getOrCreateClientId() {
    const result = await chrome.storage.local.get('clientId');
    clientId = result.clientId;
    if (!clientId) {
        // Generate a unique client ID, the actual value is not relevant
        clientId = self.crypto.randomUUID();
        await chrome.storage.local.set({ clientId });
    }
    if (!versionId) {
        const resultSettings = await chrome.storage.local.get('settings');
        log.debug("Retrieved settings: " + JSON.stringify(resultSettings));
        log.debug("Retrieved version id: " + resultSettings.settings.ext_version);
        versionId = resultSettings['ext_version'];
    }
    return clientId;
}
export async function triggerInstalledEvent() {
    fetch(`https://www.google-analytics.com/mp/collect?measurement_id=${measurement_id}&api_secret=${api_secret}`, {
        method: "POST",
        body: JSON.stringify({
            client_id: await getOrCreateClientId(),
            events: [{
                    name: 'ecollm_extension_installed_w_version',
                    params: { version: chrome.runtime.getManifest().version },
                }]
        })
    }).then((response) => {
        if (response.ok) {
            log.debug("response.ok" + JSON.stringify(response));
            return response;
        }
        throw new Error('Something went wrong');
    })
        .then((responseJson) => {
        log.debug(JSON.stringify(responseJson));
    })
        .catch((error) => {
        console.log("error" + error);
    });
}
export function setupAnalytics() {
    log.debug("Setting up google analytics.");
    log.debug(`fetched from : https://www.google-analytics.com/mp/collect?measurement_id=${measurement_id}&api_secret=${api_secret}`);
}
