export async function sendEvent(eventName, inParams) {
    log.debug("Sending event to GA " + eventName + " with params: " + JSON.stringify(inParams));
    fetch(`https://www.google-analytics.com/mp/collect?measurement_id=${measurement_id}&api_secret=${api_secret}`, {
        method: "POST",
        body: JSON.stringify({
            client_id: await getOrCreateClientId(),
            events: [{
                    name: eventName,
                    params: inParams,
                }]
        })
    }).then((response) => {
        if (response.ok) {
            log.debug("response.ok" + JSON.stringify(response));
            return response;
        }
        throw new Error('Something went wrong');
    })
        .then((responseJson) => {
        log.debug(JSON.stringify(responseJson));
    })
        .catch((error) => {
        console.log("error" + error);
    });
}
