function openRelatedTab(url) {
    if (!url) {
        console.error("URL is required to open a related tab.");
        return;
    }
    chrome.runtime.sendMessage({ type: 'OPEN_TAB', url: url }, (response) => {
        if (chrome.runtime.lastError) {
            console.error("Error sending message to background script:", chrome.runtime.lastError);
        }
        else {
            console.log("Message sent to background script to open tab:", url);
        }
    });
}
