import computeLlmImpacts from "@genai-impact/ecologits.js/es/impacts/dag.js";
import { DEFAULT_IMPACT } from "@genai-impact/ecologits.js/es/impacts/default.js";
import { log4TSProvider } from "./config/ConfigLog4j";
const log = log4TSProvider.getLogger("ecologits-wrapper.ts");
const url = "https://raw.githubusercontent.com/genai-impact/ecologits/refs/tags/0.5.0/ecologits/data/models.csv";
class EcoLogitsData {
    /**
   * The static getter that controls access to the singleton instance.
   *
   * This implementation allows you to extend the Singleton class while
   * keeping just one instance of each subclass around.
   */
    static get instance() {
        if (!EcoLogitsData.ecologitsData) {
            log.debug("EcoLogitsData: starting to fetch data !");
            fetch(url).then((res) => {
                log.debug("EcoLogitsData: fetch answer !");
                return res.text().then((text) => this.ecologitsData = new EcoLogitsData(text
                    .split("\n")
                    .slice(1, text.length)
                    .reduce((acc, line) => {
                    if (!line || typeof line !== "string" || !line.includes(",")) {
                        return acc;
                    }
                    const infos = line.split(",");
                    if (!infos[2] || !infos[3]) {
                        return acc;
                    }
                    return [
                        ...acc,
                        {
                            provider: infos[0],
                            name: infos[1],
                            totalParameters: infos[2].split(";").map((x) => parseFloat(x)),
                            activeParameters: infos[3].split(";").map((x) => parseFloat(x)),
                            warnings: infos[4],
                            sources: infos[5],
                        },
                    ];
                }, [])));
            });
        }
        return this.ecologitsData;
    }
    constructor(data) {
        this.data = [];
        log.debug("building EcologitsData object with " + data.length + " models.");
        if (data.length === undefined) {
            throw new Error("Cannot be called directly. Use build() instead.");
        }
        chrome.storage.local.set({ ecologitsData: data });
        this.data = data;
    }
    findModel(provider, name) {
        return this.data.find((model) => model.provider === provider && model.name === name);
    }
    average(arr) {
        return arr.reduce((a, b) => a + b, 0) / arr.length;
    }
    computeLlmImpacts(provider, modelName, outputTokenCount, requestLatency) {
        const model = this.findModel(provider, modelName);
        if (!model) {
            return DEFAULT_IMPACT;
        }
        const modelActiveParams = this.average(model.activeParameters);
        const modelTotalParams = this.average(model.totalParameters);
        return computeLlmImpacts(modelActiveParams, modelTotalParams, outputTokenCount, requestLatency);
    }
}
EcoLogitsData.calculateAverage = (array) => {
    const sum = array.reduce((a, b) => a + b);
    return sum / array.length;
};
export default EcoLogitsData;
