export class EnvironmentalImpact {
    constructor(gCO2_per_kWh) {
        this.gCO2_per_kWh = gCO2_per_kWh;
    }
    getCO2eq(gCO2) {
        let CO2InGrams = gCO2;
        let formattedValue = `${Math.round(CO2InGrams)} g`;
        if (CO2InGrams > 1000) {
            let CO2InKiloGrams = CO2InGrams / 1000;
            let formattedValue = `${Math.round(CO2InKiloGrams)} kg`;
            if (CO2InKiloGrams > 1000) {
                let CO2InTons = CO2InKiloGrams / 1000;
                let formattedValue = `${Math.round(CO2InKiloGrams)} tons`;
            }
        }
        return formattedValue;
    }
    getEnergy(energy_kWh) {
        let EnergyInWh = energy_kWh * 1000;
        let formattedValue = `${Math.round(EnergyInWh)} Wh`;
        if (EnergyInWh > 1000) {
            let EnergyInkWh = EnergyInWh / 1000;
            let formattedValue = `${Math.round(EnergyInkWh)} kWh`;
        }
        return formattedValue;
    }
    getWalkingDistance(energy_kWh) {
        //https://huggingface.co/spaces/genai-impact/ecologits-calculator/blob/7f50b6e8dac3143eac47344138bc9b7b69242db4/src/utils.py#L63
        let DistanceInMeters = energy_kWh * 3600 * 1000 / 196;
        let formattedValue = `${Math.round(DistanceInMeters)} m walking`;
        if (DistanceInMeters > 1000) {
            let DistanceInKiloMeters = DistanceInMeters / 1000;
            let formattedValue = `${Math.round(DistanceInKiloMeters)} km walking`;
        }
        return formattedValue;
    }
    getRunningDistance(energy_kWh) {
        let DistanceInMeters = energy_kWh * 3600 * 1000 / 294;
        let formattedValue = `${Math.round(DistanceInMeters)} m running`;
        if (DistanceInMeters > 1000) {
            let DistanceInKiloMeters = DistanceInMeters / 1000;
            let formattedValue = `${Math.round(DistanceInKiloMeters)} km running`;
        }
        return formattedValue;
    }
    getTvTime(energy_kWh) {
        const kgCO2 = energy_kWh * this.gCO2_per_kWh / 1000;
        //https://github.com/mlco2/codecarbon/blob/d3ac941202fcbb6c3d11f478c3be9802ab13a2dd/codecarbon/viz/data.py#L67
        // Ratio is 0.097 kg CO2 / 1 hour tv
        let timeInMinutes = kgCO2 / 0.097 * 60;
        let formattedValue = `${Math.round(timeInMinutes)} minutes`;
        if (timeInMinutes >= 60) {
            let timeInHours = timeInMinutes / 60;
            formattedValue = `${Math.round(timeInHours)} hours`;
            if (timeInHours >= 24) {
                let timeInDays = timeInHours / 24;
                formattedValue = `${Math.round(timeInDays)} days`;
            }
        }
        return formattedValue;
    }
    getHouseholdFraction(energy_kWh) {
        const kgCO2 = energy_kWh * this.gCO2_per_kWh / 1000;
        //https://github.com/mlco2/codecarbon/blob/d3ac941202fcbb6c3d11f478c3be9802ab13a2dd/codecarbon/viz/data.py#L86
        // 160.58kg / week
        let FractionInDays = kgCO2 / 160.58 * 100 * 7;
        let formattedValue = `${Math.round(FractionInDays)}%`;
        if (FractionInDays > 100) {
            let FractionInWeeks = FractionInDays / 7;
            let formattedValue = `${Math.round(FractionInWeeks)}%`;
            if (FractionInWeeks > 100) {
                let FractionInMonths = FractionInWeeks / (365 / 12 / 7);
                let formattedValue = `${Math.round(FractionInMonths)}%`;
            }
        }
        return formattedValue;
    }
    getHouseholdFractionUnit(energy_kWh) {
        const kgCO2 = energy_kWh * this.gCO2_per_kWh / 1000;
        //https://github.com/mlco2/codecarbon/blob/d3ac941202fcbb6c3d11f478c3be9802ab13a2dd/codecarbon/viz/data.py#L86
        // 160.58kg / week
        let FractionInDays = kgCO2 / 160.58 * 100 * 7;
        let formattedValue = `${Math.round(FractionInDays)}% US household`;
        if (FractionInDays > 100) {
            let FractionInWeeks = FractionInDays / 7;
            let formattedValue = `${Math.round(FractionInWeeks)}% US household`;
            if (FractionInWeeks > 100) {
                return "monthly";
            }
            return "weekly";
        }
        return "yearly";
    }
    getCarDistance(energy_kWh) {
        const kgCO2 = energy_kWh * this.gCO2_per_kWh / 1000;
        //https://www.eea.europa.eu/en/analysis/indicators/co2-performance-of-new-passenger/average-co2-emissions-from
        // 106.4g CO2/km => 0.1064 kg CO2/m
        let DistanceInMeters = (kgCO2 / 0.1064) * 1000;
        let formattedValue = `${Math.round(DistanceInMeters)}`;
        if (DistanceInMeters > 1000) {
            let DistanceInKiloMeters = DistanceInMeters / 1000;
            let formattedValue = `${Math.round(DistanceInKiloMeters)}`;
        }
        return formattedValue;
    }
    getCarDistanceUnit(energy_kWh) {
        const kgCO2 = energy_kWh * this.gCO2_per_kWh / 1000;
        //https://www.eea.europa.eu/en/analysis/indicators/co2-performance-of-new-passenger/average-co2-emissions-from
        // 106.4g CO2/km => 0.1064 kg CO2/m
        let DistanceInMeters = (kgCO2 / 0.1064) * 1000;
        if (DistanceInMeters > 1000) {
            return "km";
        }
        return "meters";
    }
    getLEDLightBulbTime(energy_kWh) {
        // https://particuliers.engie.fr/economies-energie/conseils-economies-energie/conseils-calcul-consommation/consommation-ampoule.html
        const hours = energy_kWh * 1000 / 5;
        const minutes = hours * 60;
        return "" + minutes.toFixed(0);
    }
    getStreamingTimeInMinutes(gwpInKg) {
        // https://impactco2.fr/outils/usagenumerique/streamingvideo 222g = 7h
        return '' + (gwpInKg / 0.222 * 7 * 60).toFixed(2);
    }
}
